/*
    mIcons (also known as symbols) will give you the option to use Material Icons throughout your application! 
    You can choose between Filled, Outlined, Rounded and Sharp.
    You may make changes here if your like. This is designed to work straight out of the box.

    Author: Maurits de Koning-Hewitt, The Orange Force
    Version: 2.1.1
*/

// Enable tweak for sidebar navigation icons
$m-icons-enable-sidebar-tweak: true;
$m-icons-sidebar-icon-size: 24px;

// Enable tweak for default tabs do you can add an icon via Name/ID
$m-icons-enable-tabs-tweak: true;

// Default icon font when none is selected

$filled: "mIcons$mIcons_Filled";
$outlined: "mIcons$mIcons_Outlined";
$rounded: "mIcons$mIcons_Rounded";
$sharp: "mIcons$mIcons_Sharp";

$m-icons-default: $outlined;

